<template>
  <span>
    <section>
      <div class="container py-4 mb-3">
        <div class="row">
          <div
            id="make-margin"
            class="
              col-xl-4 col-lg-6
              mx-auto
              d-flex
              justify-content-center
              flex-column
              px-6
              py-7
            "
          >
            <h3 class="text-center">My Profile</h3>
            <div class="card-body">
              <div class="row">
                <div class="col-md-12 pt-5">
                  <label><h6>Email</h6></label>
                  <div class="input-group">
                    <input
                      type="email"
                      class="form-control"
                      :value="user.email"
                      readonly
                    />
                  </div>
                </div>
              </div>
              <div class="row">
                <div class="col-md-12">
                  <label><h6>Name</h6></label>
                  <div class="input-group row">
                    <div class="col-9">
                      <input
                        type="name"
                        class="form-control"
                        v-model="userName"
                      />
                    </div>
                    <div class="col-3">
                      <button
                        type="button"
                        class="btn bg-gradient-dark"
                        @click="modifyName"
                      >
                        Modify
                      </button>
                    </div>
                  </div>
                </div>
              </div>
              <div class="row">
                <div class="col-md-12 text-center mt-5">
                  <br />
                  <button
                    type="button"
                    class="text-danger btn btn-outline-danger w-50"
                    data-bs-toggle="modal"
                    data-bs-target="#modal-notification"
                  >
                    Withdraw
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
    <WithdrawModal></WithdrawModal>
  </span>
</template>
<script>
import WithdrawModal from './components/WithdrawModal.vue';
import { updateUserName } from '@/api/users.js';
import store from '@/store';

export default {
  name: 'MyInfo',
  components: { WithdrawModal },
  data() {
    return {
      userName: store.state.users.user.name,
    };
  },
  computed: {
    user() {
      return store.getters['users/getUser'];
    },
  },
  methods: {
    modifyName() {
      let message = '';
      if (!this.userName) {
        message = '  ';
        this.$toastError(message);
        return;
      }
      let userData = {
        name: this.userName,
      };
      updateUserName(this.user.userId, userData)
        .then(({ status }) => {
          console.log(status);
          if (status != 200) {
            this.$toastError('   .');
            return;
          } else {
            this.$toastSuccess(' .');
            store.dispatch('users/SET_NAME', this.userName);
          }
        })
        .catch(() => {
          this.$toastError('   .');
        });
    },
  },
};
</script>

<style scoped>
#make-margin {
  padding-left: 100px !important;
}
.left-wing {
  display: inline-block;
  flex-direction: column;
}
.my-info-box {
  display: flex;
  justify-content: center;
}
</style>
