<template>
  <div>
    <AppNav />
    <nav
      class="
        navbar navbar-expand-sm
        blur blur-rounded
        top-0
        border-bottom
        z-index-3
        shadow
        w-100
        mt-1
        my-1
        py-1
      "
      style="cursor: pointer"
    >
      <div class="container-fluid">
        <a
          class="navbar-brand font-weight-bolder ms-3"
          rel="tooltip"
          data-placement="bottom"
          target="_blank"
        >
          {{ $route.params.name }}
        </a>
        <button
          class="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navigation"
          aria-controls="navigation"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span class="navbar-toggler-icon"></span>
        </button>
        <div class="collapse navbar-collapse" id="navigation">
          <ul class="navbar-nav navbar-nav-hover mx-auto">
            <li class="nav-item px-3" @click="savePPT()">
              <a class="nav-link"> Save all</a>
            </li>

            <li class="nav-item px-3" @click="goBackPresentation()">
              <a class="nav-link"> Back to the list </a>
            </li>

            <li class="nav-item px-3" @click="showAllPPT()">
              <a class="nav-link"> Preview </a>
            </li>
          </ul>

          <ul class="navbar-nav">
            <button
              class="btn btn-sm bg-gradient-dark btn-round mb-0 me-1"
              @click="animationTabToggle('hi')"
            >
              Animation
            </button>
          </ul>
        </div>
      </div>
    </nav>
    <!-- End Navbar -->
    <div id="modify-ppt">
      <div class="head-ppt">
        <div class="container-fluid">
          <div class="head-animation" v-if="showAnimation == true">
            <div
              v-for="(effect, idx) in effects"
              :class="['effect-container']"
              :key="idx"
              :id="idx"
              @click="showExample(effect, idx)"
            >
              <h6>{{ idx }}</h6>
            </div>
          </div>
        </div>
      </div>
      <div class="body-ppt">
        <div class="col-3 ppt-overflow">
          <!--    @end="updateItemOrder -->
          <!-- <draggable v-model="slideList" @end="updateItemOrder"> -->
          <draggable v-model="slideList">
            <transition-group tag="div" class="choose-ppt card">
              <div
                class="choose-ppt card"
                v-for="(slide, idx) in slideList"
                :key="`key-${slide.slideId}`"
                @click="setIdx(idx)"
              >
                <div class="choose-ppt-icons">
                  <i
                    class="ni ni-fat-remove"
                    @click="deleteTheSlide(slide.slideId)"
                  ></i>
                  <i class="ni ni-spaceship"></i>
                </div>
                <div class="PPTbox">
                  <div style="width: 100%; height: 150px">
                    <img
                      :src="slide.slideFile"
                      style="max-width: 20vw; max-height: 9vw"
                      alt="thumbnail"
                    />
                  </div>
                </div>
              </div>
            </transition-group>
          </draggable>
          <div class="choose-ppt card">
            <div class="PPTbox">
              <div style="width: 100%; height: 150px">
                <label
                  >Add new slide
                  <button
                    type="button"
                    data-bs-toggle="modal"
                    data-bs-target="#AddSlideModal"
                  />
                </label>
                <img
                  class="image thumbnail-setting"
                  :src="imgUrl.first"
                  alt=""
                  @click="dialogVisible.first = true"
                />
              </div>
            </div>
          </div>
        </div>
        <div class="col-9 body-margin-top">
          <div class="body-main">
            <div>
              <img
                :src="slideList[idx].slideFile"
                style="max-width: 60vw; max-height: 50vh; margin: 5px"
                alt="animations"
              />
            </div>
          </div>
          <div>
            <Editor
              ref="toastuiEditor"
              :options="editorOptions"
              height="300px"
              initialEditType="wysiwyg"
              previewStyle="vertical"
              @blur="saveEditorText"
            />
          </div>
          <img id="ItemPreview" src="" />
        </div>
      </div>
    </div>
    <AddSlideModal></AddSlideModal>
  </div>
</template>

<script>
import draggable from 'vuedraggable';
import AppNav from '@/components/common/AppNav.vue';
import AddSlideModal from './components/AddSlideModal.vue';
import {
  getPresentationDetail,
  savePresentation,
  addSlide,
  deleteSlide,
} from '@/api/presentation.js';
import { mapActions } from 'vuex';
import { updateScript } from '@/api/slide.js';
import { Editor } from '@toast-ui/vue-editor';
import store from '@/store';

export default {
  name: 'ModifyPresentation',
  components: { draggable, AppNav, AddSlideModal, Editor },
  data() {
    return {
      idx: 0,
      presentationId: this.$route.params.presentation_id,
      userId: store.state.users.user.userId,
      debounce: false,
      effects: {
        default: 0,
        fadein: 1,
        fadedown: 2,
        fadeleft: 3,
        faderight: 4,
        fadeup: 5,
        backdown: 6,
        backup: 7,
        flipx: 8,
        flipy: 9,
        rotatein: 10,
      },
      presentationName: null,
      size: null,
      uploadTime: null,
      slideList: [
        {
          effect: null,
          script: null,
          sequence: null,
          slideFile: null,
          slideId: null,
        },
      ],
      sendSlideList: [],
      items: [{ title: 'PPT ' }, { title: 'PPT ' }],
      editorOptions: {
        minHeight: '200px',
        hideModeSwitch: true,
        toolbarItems: [
          ['heading', 'bold', 'italic', 'strike'],
          ['hr', 'quote'],
          ['ul', 'ol', 'task', 'indent', 'outdent'],
          ['scrollSync'],
        ],
      },
      slideId: 1, //         (  )
      editorText: '',
      showAnimation: false,
      dialogVisible: { first: false },
      imgUrl: { first: '' },
    };
  },
  computed: {
    messageData() {
      const data = {
        id: 'changePresentation',
        currentPage: store.state.mypage.currentPage,
        location: store.state.mypage.location,
        size: store.state.mypage.size,
      };
      return data;
    },
  },
  methods: {
    ...mapActions('mypage', ['setSequenceNum']),
    // DB Presentation  Slide .
    //     ,  mounted!
    async getPresentationData() {
      let response = await getPresentationDetail(
        this.userId,
        this.presentationId,
      );
      // ByteArray img 
      let imgByteArray = response.data.slideList;
      imgByteArray.forEach(element => {
        element.slideFile = 'data:image/png;base64,' + element.slideFile;
      });
      // slideList 
      this.slideList = response.data.slideList;
      console.log(' slideList: ', this.slideList);
      //  
      if (this.slideList[0].slideId === null) return;
      this.editorText = this.slideList[0].script;
      this.$refs.toastuiEditor.invoke('setHTML', this.editorText);
    },
    // Presentation .
    goBackPresentation() {
      this.$router.push({ name: 'Presentation' });
    },
    // Effect  .
    showExample(idx, effect) {
      console.log(effect);
      const el = document.getElementById(effect);
      el.classList.add(effect);
      setTimeout(function () {
        el.classList.remove(effect);
      }, 1000);
      this.selectEffect(idx);
    },
    //  Effect  .
    selectEffect(effect) {
      let num = this.idx;
      this.slideList[num].effect = effect;
    },
    //    
    showAllPPT() {
      this.$router.push({
        name: 'PresentationPreview',
        params: {
          userId: this.userId,
          presentationId: this.presentationId,
        },
      });
    },
    //  .
    async savePPT() {
      try {
        await this.updateItemOrder(); // Drag&Drop    
        let presentationId = this.presentationId;
        let userId = store.getters['users/getUserId'];
        // console.log('presentationId: ', presentationId);
        // console.log('userId: ', userId);
        let data = {
          userId,
          presentationId,
          slides: this.sendSlideList,
        };
        console.log(' data: ', data);
        await savePresentation(data);
        await this.$toastSuccess(' .');
        this.$router.go();
      } catch (exp) {
        console.log(exp);
        this.$toastError('  .');
      }
    },
    //   Idx  (Effect  )
    setIdx(num) {
      this.idx = num;

      console.log('before getslide slidelist: ', this.slideList);
      // getSlide(this.slideList[this.idx].slideId).then(res => {
      //   if (res.status != 200) {
      //     this.$toastError(
      //       '     .',
      //     );
      //     return;
      //   } else {
      //     this.editorText = res.data.script;
      //     this.$refs.toastuiEditor.invoke('setHTML', res.data.script);
      //   }
      // });
      this.editorText = this.slideList[this.idx].script;
      this.$refs.toastuiEditor.invoke('setHTML', this.editorText);
    },
    // PPT  .
    addnewSlide() {
      // Modal 

      let userId = this.userId;
      let presentationId = this.presentationId;
      let data = {
        userId,
        presentationId,
      };
      addSlide(data);
    },
    // PPT .
    deleteTheSlide(slideId) {
      try {
        deleteSlide(slideId);
        this.$toastSuccess(' .');
        this.$router.go();
      } catch (exp) {
        this.$toastError('  .');
      }
    },
    updateItemOrder: function () {
      // get your info then...
      let items = this.slideList.map(function (slide, index) {
        return {
          script: slide.script,
          sequence: index,
          slideId: slide.slideId,
          effect: slide.effect,
        };
      });
      console.log(items);
      this.sendSlideList = items;
      if (this.debounce) return;
      this.debounce = function (items) {
        this.debounce = false;
        // send it to your db
        console.log(items);
      }.bind(this, items);
    },
    saveEditorText() {
      let curEditorText = this.$refs.toastuiEditor.invoke('getHTML');
      if (this.editorText === curEditorText) return;

      let updateScriptReq = {
        slideId: this.slideList[this.idx].slideId,
        script: this.$refs.toastuiEditor.invoke('getHTML'),
      };

      updateScript(updateScriptReq).then(res => {
        if (res.status != 200) {
          this.$toastError(
            '    .    .',
          );
          return;
        } else {
          this.$toastSuccess(' .');
          this.editorText = curEditorText;
          this.slideList[this.idx].script = curEditorText;
        }
      });
    },
    animationTabToggle(message) {
      console.log(message);
      this.showAnimation = !this.showAnimation;
      console.log('show animation: ', this.showAnimation);
    },
  },
  created() {},
  //     
  mounted() {
    this.getPresentationData();
  },
};
</script>

